.featured{
    width: 100%;
    display: flex;
    justify-content:space-between;

}

.featuredItem{
    flex: 1;
    margin: 0px 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    -webkit-box-shadow: 0px 0px 20px 2px #C1C1C1; 
    box-shadow: 0px 0px 20px 2px #C1C1C1;
    margin-top: 30px;
}

.featuredTitle{
    font-size: 20px;
}

.featuredTemperatureContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.featuredTemperature{
    font-size: 30px;
    font-weight: 600;
    display: flex;
    align-items: center;
    margin-left: 20px;

}

.featuredTemperatureLevel{
    font-size: 30px;
    font-weight: 600;
    display: flex;
    align-items: center;
}

.featuredTemperatureState{
    font-size: 15px;
    font-weight: 400;
    display: flex;
    align-items: center;
    color: gray;
}

.featuredIcon{
    font-size: 30px;
    margin-left: 5px;
    color: green;
}

.featuredIcon.negative{
    color: red;
}

.summary_title{
    font-weight: 600;
    font-size: 30px;
    color: rgb(126, 126, 126);
    padding: 0px 20px;
     
}

